import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { PromissoryNotePeriod } from 'app/core/models/promissory-note-period.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-promissory-note-period',
  templateUrl: './promissory-note-period.component.html',
  styleUrls: ['./promissory-note-period.component.scss']
})
export class PromissoryNotePeriodComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<PromissoryNotePeriod>;
  displayedColumns: string[] = ['noOfDays'];

  promissoryNotePeriod = new PromissoryNotePeriod();
  promissoryNotePeriodList = [];

  promissoryNotePeriodForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.promissoryNotePeriods).subscribe((res: any) => {
      this.promissoryNotePeriodList = res.responseData.data;
      this.dataSource = new MatTableDataSource<PromissoryNotePeriod>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.promissoryNotePeriods, this.promissoryNotePeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        if (!Number(this.promissoryNotePeriod.noOfDays)) {
          err.error.message = 'Please enter numeric values only';
        } else {
          err.error.message = 'Default Promissory Note Period have already been set. Select the entry to update the record';
        }
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getPromissoryNotePeriod(): void {
    this.apiService.findById(ENDPOINTS.promissoryNotePeriods, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.promissoryNotePeriod = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.promissoryNotePeriods, this.promissoryNotePeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.promissoryNotePeriodForm = this.formBuilder.group({
      noOfDays: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.promissoryNotePeriod = new PromissoryNotePeriod();
    this.promissoryNotePeriodForm.markAsUntouched();
    this.promissoryNotePeriodForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this promissory note period?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.promissoryNotePeriods, this.promissoryNotePeriod.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Promissory note period successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
